#about {
    min-height: 100vh;
}

.about-header {
    display: inline;
    position: relative;
    top: 17vh;
    right: -35vw;
}

.about-summary {
    background-color: #fff;
    border-radius: 10px;
    margin: auto;
    position: relative;
    top: 19vh;
    right: -2vw;
    width: 250px; /* Height is being handled with framer-motion */
    text-align: center;
    font-size: 13px;
}

.about-summary p {
    padding-bottom: 1vh;
}

@media screen and (min-width: 600px) { /* Tablet */
    .about-header {
        right: -21vw;
    }

    .about-summary {
        right: 23vw;
    }
}

@media screen and (min-width: 1000px) { /* Desktop */
}